
@media (max-width: 1100px) {
	.site-header__menu-item {
		font-size: 16px;
	}
}

@media (max-width: 993px) {
	.dekstop-none {
		display: block;
	}
	.site-header__logo {
		svg {
			height: 35px;
			width: 130px;
		}
	}
	.add-subscribe {
		display: none !important;
	}
	.mobile__title {
		display: block;
		margin-top: 20px;
		margin-bottom: 20px;
	}
	.mobile-menu-not-registered {
		position: fixed;
		z-index: 3;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;
		display: none;
		height: calc(100% - 63px);
		&__wrap {
			padding: 35px;
			height: 100%;
			box-sizing: border-box;
			overflow-y: scroll;
		}
		&__button {
			display: block;
			max-width: 330px;
			height: 60px;
			line-height: 60px;
			background-color: #CF5CFF;
			text-align: center;
			box-shadow: 0px 10px 30px rgba(122, 138, 235, 0.25);
			border-radius: 16px;
			font-family: $circe;
			color: #FFFFFF;
			font-size: 14px;
			font-weight: 700;
			transition: all .3s ease;
			&:hover, &:active {
				background-color: darken(#CF5CFF, 10%);
				transition: all .3s ease;
			}
		}
		&__menu {
			margin-bottom: 50px;
			&-item {
				display: block;
				margin-bottom: 38px;
				font-family: $ubuntu;
				color: #242328;
				font-weight: 500;
				&--active {
					font-size: 18px;
				}
			}
		}
	}
	.site-header__menu {
		display: none;
	}
	.site-header__login {
		display: none;
	}
	.mobile-menu-header {
		display: flex;
		align-items: center;
		&__item {
			margin-right: 40px;
		}
		&__hum {
			svg {
				transition: all .3s ease;
				height: 55px;
			}
			&--active {
				svg {
					rect {
						&:nth-child(1) {
							display: none;
						}
						&:nth-child(2) {
							transform: rotate(45deg) translateY(-3px) translateX(2px)
						}
					}
					path {
						transform: rotate(-45deg) translateX(-8px) translateY(5px);
					}
				}
			}
		}
	}
	.site-header__wrap {
		padding-left: 25px;
		padding-right: 30px;
		height: 100%;
	}
	.site-header {
		box-sizing: border-box;
		height: 62px;
		padding: 0;
		border-bottom: 1px solid rgba(#000, .1);
	}
}

@media (max-width: 660px) {
	.registration {
		max-width: calc(100% - 50px);
		margin: 0 25px;
		margin-top: 100px;
		margin-bottom: 40px;
	}
	.anketa__inner {
		flex-wrap: wrap;
	}
	.anketa__title {
		flex: 0 0 100%;
	}
	.anketa__inner .input-check {
		margin-top: 15px;
	}
}

@media (max-width: 510px) {
	.pr-55 {
		padding-right: 25px !important;
	}
}

@media (max-width: 460px) {
	.registration {
		padding-left: 40px;
		padding-right: 40px;
	}
}

@media (max-width: 400px) {
	.registration__title {
		margin-bottom: 70px;
	}
	.registration__politic {
		margin-top: 30px;
	}

}

@media (max-width: 374px) {
	.mobile-menu-header__item {
		margin-right: 20px;
	}
}