.settings {
	&__wrap {
		display: flex;
	}
	&__content {
		max-width: 60%;
		flex: 0 0 60%;
		margin-right: 5%;
		.anketa {
			margin-top: 0;
			.registration__title {
				text-align: left;
				font-weight: bold;
			}
			.form > .anketa__title {
				display: none;
			}
			.form-button {
				background: #CF5CFF;
				&:hover, &:active {
					background: darken(#CF5CFF, 10%);
				}
			}
		}
	}
	&__sidebar {
		max-width: 35%;
		flex: 0 0 35%;
	}
}

@media (max-width: 993px) {
	.settings__wrap {
		flex-wrap: wrap;
	}
	.settings__content {
		max-width: 100%;
		flex: 0 0 100%;
		.anketa {
			margin-left: auto;
			margin-right: auto;
			width: 100%;
			max-width: 100%;
			margin-bottom: 0;
		}
	}
	.settings__sidebar {
		max-width: 100%;
		flex: 0 0 100%;
		margin-top: 45px;
	}
}