.menu-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 12px;
	margin-bottom: 12px;
	transition: all .3s ease;
	&--active, &:hover, &:active {
		background: #F3FBF8;
		border-radius: 8px;
		transition: all .3s ease;
	}
	&__block {
		display: flex;
		align-items: center;
	}
	&__img {
		flex: 0 0 24px;
		height: 24px;
		margin-right: 15px;
	}
	&__title {
		color: #242328;
		font-family: $ubuntu;
		font-size: 16px;
		font-weight: 600;
	}
	&__num {
		flex: 0 0 24px;
		height: 24px;
		text-align: center;
		line-height: 24px;
		font-family: $ubuntu;
		font-size: 12px;
		font-weight: bold;
		color: #CA49FF;
		background: rgba(202, 73, 255, 0.2);
		border-radius: 6px;
		display: block;
	}
}