.promo-block {
	background: #FFFFFF;
	border: 1px solid #F3FBF8;
	box-sizing: border-box;
	border-radius: 48px;
	padding: 60px 50px;
	margin: 50px 0;
	padding-right: 0;
	margin-right: 55px;
	&__title {
		font-size: 32px;
		line-height: 106.5%;
		color: #000000;
		font-family: $circe;
		margin-bottom: 35px;
		font-weight: bold;
	}
	&__wrap {
		display: flex;
		overflow-x: scroll;
		&::-webkit-scrollbar {
			width: 0px; /* ширина для вертикального скролла */
			height: 0px;/* высота для горизонтального скролла */
		}
	}
	&__item {
		flex: 0 0 350px;
		margin-right: 25px;
		&-img {
			height: 470px;
			margin-bottom: 15px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				vertical-align: bottom;
			}
		}
		&-title {
			font-weight: bold;
			font-size: 24px;
			line-height: 106.5%;
			font-family: $circe;
			margin-bottom: 15px;
			height: 50px;
			color: #000000;
			margin-bottom: 15px;
			display: block;
		}
		&-opt {
			display: flex;
			align-items: center;
		}
		&-icon {
			display: flex;
			align-items: center;
			font-family: $ubuntu;
			margin-right: 10px;
			font-weight: normal;
			font-size: 12px;
			color: rgba(#2B2D33, .5);
			svg {
				margin-right: 5px;
			}
		}
		&-time {
			margin-right: 10px;
			font-weight: normal;
			font-size: 12px;
			opacity: 0.5;
			color: #2B2D33;
			font-family: $ubuntu;
		}
	}
}

@media (max-width: 993px) {
	.promo-block {
		padding: 0;
		background-color: transparent;
		margin-right: 0;
	}
	.promo-block__title {
		font-family: "Circe Rounded",sans-serif;
    font-size: 18px;
    line-height: 106.5%;
    color: #1c2f76;
    font-weight: 600;
	}
}

@media (max-width: 700px) {
	.promo-block__item {
		flex: 0 0 160px;
	}
	.promo-block__item-img {
		height: 200px;
		img {
			border-radius: 20px;
		}
	}
	.promo-block__item-title {
		font-size: 16px;
		height: 48px;
		overflow: hidden;
	}
}