.change-password {
	background-color: #fff;
	border: 1px solid #F3FBF8;
	box-sizing: border-box;
	border-radius: 48px;
	padding: 55px;
	padding-bottom: 50px;
	padding-top: 48px;
	margin-bottom: 45px;
	.form-input {
		position: relative;
		svg {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 0;
		}
	}
	&__title {
		text-align: left;
		white-space: nowrap;
    font-weight: 700;
    font-size: 32px;
    font-family: "Circe Rounded",sans-serif;
    color: #1c2f76;
    margin-bottom: 30px;
	}
	.form-button {
		background: #CF5CFF;
		margin-top: 15px;
		&:hover, &:active {
			background: darken(#CF5CFF, 10%);
		}
	}
}

@media (max-width: 993px) {
	.change-password {
		max-width: 675px;
		margin: auto;
		margin-bottom: 45px;
	}
}