.sub-status {
	background: #F5FDFC;
	border-radius: 9px;
	position: relative;
	padding: 10px 14px;
	box-sizing: border-box;
	font-family: $ubuntu;
	&__title {
		font-size: 12px;
		line-height: 1.22;
	}
	&__wrap {
		position: relative;
		margin-top: 10px;
		margin-bottom: 15px;
		&--sub {
			.sub-status__pay {
				height: 44px;
				line-height: 55px;
			}
		}
		&--green {
			.sub-status__pay {
				background-color: #7ADE69;
			}
		}
	}
	&__border {
		background: linear-gradient(179.06deg, #CA49FF 0.72%, #3CDCC9 99.11%);
		position: relative;
		border-radius: 10px;
		width: 100%;
		padding: 1px;
	}
	&__title {
		color: #242328;
		font-size: 12px;
		font-family: $ubuntu;
		margin-bottom: 3px;
	}
	&__price {
		display: flex;
		&-item {
			font-size: 12px;
			font-weight: bold;
			font-family: $ubuntu;
			color: #242328;
			margin-right: 10px;
		}
		&-sale {
			text-decoration-line: line-through;
			font-size: 10px;
			color: rgba(#242328, .4);
			font-weight: bold;
			font-family: $ubuntu;
		}
	}
	&__pay {
		text-align: center;
		height: 28px;
		margin-top: -11px;
		font-size: 10px;
		color: #fff;
		background: #CA49FF;
		border-radius: 6px;
		font-family: $ubuntu;
		line-height: 35px;
		cursor: pointer;
	}
}

@media (max-width: 400px) {
	.sub-status__title {
		font-size: 10px;
	}
}