.add-subscribe {
	background-image: url(../img/sub.png);
	min-height: 140px;
	border-radius: 32px;
	background-position: center right;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 30px;
	&__title {
		background: linear-gradient(123.5deg, #CA49FF 12.59%, #3CDCC9 72.89%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
  	-webkit-background-clip: text;
  	-webkit-text-fill-color: transparent;
  	padding-top: 35px;
		padding-bottom: 38px;
		padding-left: 27px;
		font-size: 30px;
		line-height: 1.06;
		font-family: $circe;
		font-weight: 600;
	}
}