.consult-block {
	padding: 20px;
	border-radius: 16px;
	background-color: #F3FBF8;
	margin-bottom: 15px;
	&__progress {
		width: 100%;
		height: 6px;
		border-radius: 8px;
		background-color: #fff;
		margin-top: 13px;
		&-item {
			background: linear-gradient(115.78deg, #CA49FF 22.28%, #3CDCC9 109.93%), linear-gradient(176.18deg, #CA49FF 3.13%, #3CDCC9 105.79%), #C4C4C4;
			border-radius: 8px;
			height: 6px;
		}
		&-title {
			font-size: 12px;
			line-height: 1.22;
			color: #979797;
			margin-top: 5px;
			font-family: $ubuntu;
			strong {
				font-weight: bold;
				color: #000;
			}
		}
	}
	&__title {
		font-size: 12px;
		font-family: $ubuntu;
		font-weight: bold;
		color: #242328;
		margin-bottom: 3px;
	}
	&__wrap {
		display: flex;
		font-family: $ubuntu;
		font-size: 12px;
		align-items: center;
	}
	&__day {
		padding: 0 15px;
		height: 32px;
		box-sizing: border-box;
		border: 1px solid #0A1F6D;
		border-radius: 8px;
		line-height: 32px;
		font-family: $ubuntu;
		font-size: 12px;
		color: #242328;
		margin-left: 10px;
		white-space: nowrap;
	}
	&__opt {
		margin-left: 10px;
		cursor: pointer;
	}
	&__time {
		background: #CA49FF;
		border-radius: 8px;
		padding-left: 12px;
		padding-right: 12px;
		height: 32px;
		line-height: 32px;
		color: #FFFFFF;
		font-family: $ubuntu;
		font-weight: bold;
		margin-left: 10px;
	}
}

@media (max-width: 440px) {
	.consult-block__wrap {
		flex-wrap: wrap;
	}
	.consult-block__day {
		margin-left: 0;
	}
	.consult-block__wrap-title {
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: 10px;
	}
}