.coming-session {
	margin-top: 25px;
	margin-bottom: 50px;
	padding-right: 55px;
	&__wrap {
		display: flex;
	}
	&__link {
		background: #CA49FF;
		border-radius: 24px;
		flex: 0 0 16%;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-style: normal;
		font-weight: 700;
		font-size: 18px;
		line-height: 122%;
		color: #242328;
		font-family: $ubuntu;
		a {
			display: block;
			margin-top: 25px;
			g {
				transition: all .3s ease;
			}
			&:hover {
				g {
					opacity: 1;
					transition: all .3s ease;
				}
			}
		}
		> svg {
			margin-bottom: 20px;
		}
	}
	&__item {
		flex: 0 0 25.5%;
		margin-right: 2.5%;
		box-sizing: border-box;
		padding: 30px 0 40px;
		background-color: #fff;
		border-radius: 24px;
		text-align: center;
		&-status {
			font-family: $ubuntu;
			font-weight: normal;
			font-size: 12px;
			line-height: 122%;
			color: #979797;
		}
		&-title {
			font-family: $ubuntu;
			font-weight: bold;
			font-size: 18px;
			line-height: 122%;
			color: #0A1F6D;
			margin-bottom: 10px;
		}
		&-day {
			height: 32px;
			padding-left: 15px;
			padding-right: 15px;
			background: #F3FBF8;
			border: 1px solid #0A1F6D;
			box-sizing: border-box;
			border-radius: 8px;
			display: inline-block;
			line-height: 32px;
			font-family: $ubuntu;
			font-size: 12px;
			color: #242328;
			margin-top: 10px;
			margin-bottom: 10px;
			white-space: nowrap;
		}
		&-time {
			background: #CA49FF;
			border-radius: 8px;
			height: 32px;
			line-height: 32px;
			padding-left: 15px;
			padding-right: 15px;
			font-family: $ubuntu;
			font-weight: bold;
			font-size: 12px;
			color: #FFFFFF;
			display: inline-block;
		}
		&-img {
			width: 80px;
			height: 80px;
			text-align: center;
			margin: auto;
			margin-bottom: 30px;
			border-radius: 100%;
			padding: 2px;
			background: linear-gradient(180deg, #CA49FF 0.55%, #3CDCC9 100.55%);
			img {
				width: 100%;
				border-radius: 100%;
				height: 100%;
				object-fit: cover;
				vertical-align: bottom;
			}
		}
	}
}

@media (max-width: 1200px) {
	.banner__slide-title {
		font-size: 48px;
	}
	.coming-session__item {
		flex: 0 0 265px;
		max-width: 265px;
	}
	.coming-session__link {
		flex: 0 0 175px;
		max-width: 175px;
	}
	.coming-session__wrap {
		overflow-x: scroll;
		&::-webkit-scrollbar {
			width: 0px; /* ширина для вертикального скролла */
			height: 0px;/* высота для горизонтального скролла */
		}
	}
	.coming-session {
		padding-right: 0;
	}
}

