.services {
	padding: 40px 45px;
	background-color: #fff;
	border-radius: 32px;
	margin-top: 25px;
	&__wrap {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__item {
		max-width: 30%;
		flex: 0 0 30%;
		margin-bottom: 50px;
		&-link {
			display: none;
		}
	}
	&__title {
		font-size: 24px;
		color: #000;
		margin-bottom: 15px;
		font-family: $circe;
		font-weight: bold;
	}
	ul {
		padding-left: 0;
		margin-left: 0;
		li {
			margin-bottom: 15px;
			list-style-type: none;
			a {
				font-size: 20px;
				color: rgba(#000, .8);
				font-family: $ubuntu;
				font-weight: 500;
			}
		}
	}
	p {
		font-family: $ubuntu;
		color: #808080;
		font-size: 16px;
		a {
			color: #0A1F6D;
			text-decoration: underline;
		}
	}
}

@media (max-width: 993px) {
	.services__item {
		max-width: 45%;
		flex: 0 0 45%;
	}
}
@media (max-width: 700px) {
	.services ul {
		display: none;
	}
	.services__title {
		margin-bottom: 10px;
	}
	.services__item {
		flex: 0 0 100%;
		max-width: 100%;
		padding-bottom: 15px;
		border-bottom: 1px solid rgba(#C4C4C4, .5);
		margin-bottom: 15px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-right: 20px;
		&:last-child {
			padding-bottom: 0;
			margin-bottom: 0;
			border-bottom: 0 solid #000;
		}
	}
	.services {
		padding: 20px;
	}
	.services__item-link {
		display: block;
	}
	.services p {
		font-size: 12px;
	}
	.services__title {
		font-size: 18px;
	}
}