.callback-block {
	border-radius: 24px;
	background-color: #fff;
	background-image: url(../img/call.png);
	padding: 30px 20px 35px;
	background-repeat: no-repeat;
	background-position-x: right;
	margin-top: 15px;
	display: none;
	&__title {
		max-width: 175px;
		font-family: $circe;
		font-weight: 700;
		font-size: 17px;
		background: -o-linear-gradient(326.5deg,#ca49ff 12.59%,#3cdcc9 72.89%),-o-linear-gradient(bottom,#fff,#fff);
	  background: linear-gradient(123.5deg,#ca49ff 12.59%,#3cdcc9 72.89%),linear-gradient(0deg,#fff,#fff);
	  -webkit-background-clip: text;
	  -webkit-text-fill-color: transparent;
	}
}

@media (max-width: 993px) {
	.callback-block {
		display: block;
	}
}