.main {
	max-width: 1920px;
	margin: auto;
	box-sizing: border-box;
	padding-left: 340px;
	padding-right: 55px;
	display: flex;
	&__title {
		font-family: $circe;
		font-size: 18px;
		line-height: 106.5%;
		color: #1C2F76;
		font-weight: 600;
	}
	&__sidebar {
		flex: 0 0 210px;
		padding-top: 40px;
	}
	&__content {
		padding-top: 40px;
		max-width: calc(100% - 260px);
		flex: 0 0 calc(100% - 260px);
		margin-right: 50px;
		overflow-y: scroll;
		overflow-x: hidden;
		height: calc(100vh - 114px);
		&::-webkit-scrollbar {
			width: 0px; /* ширина для вертикального скролла */
			height: 0px;/* высота для горизонтального скролла */
		}
	}
	&--index {
		padding-right: 0;
		.main__content {
			max-width: 100%;
			flex: 0 0 100%;
			margin-right: 0;
		}
	}
}

.pr-55 {
	padding-right: 55px;
}

@media (max-width: 1400px) {
	.main {
		flex-wrap: wrap;
		height: calc(100vh - 74px);
		overflow-y: scroll;
		overflow-x: hidden;
	}
	.main__content {
		max-width: 100%;
		flex: 0 0 100%;
		height: auto;
		overflow-y: hidden;
	}
	.main__sidebar {
		flex: 0 0 100%;
		max-width: 100%;
		height: auto;
		overflow-y: hidden;
	}
}

@media (max-width: 993px) {
	.main {
		padding-left: 55px;
		height: calc(100vh - 62px);
		margin-top: 62px;
	}
	.main__content {
		padding-top: 0;
	}
	.main--index {
		padding-top: 30px;
	}
}

@media (max-width: 510px) {
	.main {
		padding-right: 25px;
		padding-left: 25px;
	}
	.main--index {
		padding-right: 0;
	}
}