.ref {
	margin-top: 25px;
	&__line {
		display: flex;
		overflow-x: scroll;
		transition: all .3s ease;
		&::-webkit-scrollbar {
			width: 0px; /* ширина для вертикального скролла */
			height: 0px;/* высота для горизонтального скролла */
		}
		&-v2 {
			.ref__item {
				flex: 0 0 325px;
			}
		}
	}
	&__item {
		flex: 0 0 475px;
		margin-right: 25px;
		margin-bottom: 20px;
		padding: 30px 35px;
		background: #3A115A;
		border-radius: 32px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 245px;
		&-button {
			height: 48px;
			border-radius: 32px;
			background-color: #fff;
			display: inline-block;
			width: 200px;
			line-height: 48px;
			text-align: center;
			font-family: $circe ;
			font-weight: normal;
			font-size: 14px;
			color: #000000;
			font-weight: 600;
			transition: all .3s ease;
			&:hover {
				background-color: darken(#fff, 5%);
				transition: all .3s ease;
			}
		}
		&-block {
			display: flex;
			justify-content: space-between;
		}
		&-img {
			display: flex;
			&-item {
				flex: 0 0 48px;
				height: 48px;
				border-radius: 100%;
				padding: 2px;
				background: linear-gradient(180deg, #CA49FF 0.55%, #3CDCC9 100.55%);
				margin-left: -20px;
				&:first-child {
					margin-left: 0;
				}
			}
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 100%;
				vertical-align: bottom;
			}
		}
		&-title {
			font-family: $ubuntu;
			font-style: normal;
			font-weight: 500;
			font-size: 18px;
			line-height: 122%;
			color: #FFFFFF;
			opacity: 0.7;
			max-width: 220px;
			height: 42px;
			overflow: hidden;
			margin-top: 10px;
		}
		&-status {
			font-family: $circe;
			font-style: normal;
			font-weight: normal;
			font-size: 32px;
			line-height: 106.5%;
			color: #FFFFFF;
		}
	}
}

@media (max-width: 993px) {
	.ref {
		margin-bottom: 10px;
	}
}

@media (max-width: 600px) {
	.ref__item {
		flex: 0 0 300px;
		padding: 15px 20px;
		height: 175px;
		padding-top: 25px;
		margin-right: 15px;
	}
	.ref__item-title {
		font-size: 10px;
	}
	.ref__item-status {
		font-size: 24px;
	}
	.ref__item-button {
		height: 32px;
		width: 130px;
		line-height: 32px;
	}
	.ref__line-v2 .ref__item {
		flex: 175px;
	}
}