.user-cart {
	display: flex;
	align-items: center;
	&__img {
		flex: 0 0 48px;
		height: 48px;
		margin-right: 10px;
		padding: 3px;
		border-radius: 100%;
		background: linear-gradient(270deg, #CA49FF 0%, #3CDCC9 100%);
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 100%;
		}
	}
	&__title {
		color: #000000;
		font-weight: 600;
		font-size: 16px;
		font-family: $ubuntu;
		line-height: 1.22;
		margin-right: 40px;
		white-space: nowrap;
	}
	&__status {
		font-size: 12px;
		font-family: $ubuntu;
		font-weight: normal;
		line-height: 1.22;
		color: rgba(#000, .6)
	}
}