.add-post {
	background: linear-gradient(0deg, #CA49FF, #CA49FF), #FFFFFF;
	opacity: 0.9;
	box-shadow: 0px 10px 30px rgba(122, 138, 235, 0.25);
	border-radius: 24px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 29px 34px;
	font-family: $circe;
	font-size: 14px;
	color: #fff;
	font-weight: 700;
	cursor: pointer;
	box-sizing: border-box;
	white-space: nowrap;
	transition: all .3s ease;
	&:hover, &:active {
		background: linear-gradient(0deg, darken(#CA49FF,10%), darken(#CA49FF,10%)), darken(#FFFFFF,10%);
		transition: all .3s ease;
	}
	svg {
		margin-right: 10px;
	}
}

@media (max-width: 440px) {
	.add-post {
		height: 60px;
		padding: 0;
		margin-bottom: 30px;
	}
}