.banner {
	position: relative;
	margin-bottom: 50px;
	&__track {
		overflow: hidden;
	}
	&__wrap {
		position: relative;
		display: flex;
		transition: all .3s ease;
	}
	&__dots {
		position: absolute;
		top: 55px;
		right: 50px;
		display: flex;
		&-item {
			width: 10px;
			height: 10px;
			margin: 0 4px;
			border-radius: 100%;
			box-sizing: border-box;
			cursor: pointer;
			border: 1px solid #fff;
			opacity: .6;
			transition: all .3s ease;
			&:hover, &:active, &--active {
				background-color: #fff;
				opacity: 1;
				transition: all .3s ease;
			}
		}
	}
	&__buttons {
		position: absolute;
		display: flex;
		right: 50px;
		bottom: 55px;
		&-item {
			margin: 0 8px;
			cursor: pointer;
			g {
				transition: all .3s ease;
			}
			&:hover, &:active, &--active {
				g {
					opacity: 1;
				}
			}
		}
	}
	&__slide {
		flex: 0 0 100%;
		height: 445px;
		border-radius: 40px;
		background-image: url(../img/banner.png);
		background-size: cover;
		background-position: center center;
		padding: 70px 60px;
		box-sizing: border-box;
		padding-bottom: 50px;
		&-title {
			font-size: 64px;
			line-height: 106.5%;
			font-family: $circe;
			color: #fff;
			margin-bottom: 30px;
			span {
				color: #8EECEE;
			}
		}
		&-text {
			font-size: 24px;
			line-height: 106.5%;
			color: #FFFFFF;
			font-family: $circe;
			margin-bottom: 60px;
		}
		&-button {
			color: #1C2F76;
			height: 64px;
			line-height: 64px;
			width: 275px;
			display: block;
			background-color: #fff;
			border-radius: 32px;
			text-align: center;
			font-size: 16px;
			font-family: $circe;
			font-weight: 600;
			transition: all .3s ease;
			&:hover, &:active {
				background-color: darken(#fff, 5%);
				transition: all .3s ease;
			}
		}
	}

}

@media (max-width: 993px) {
	.banner__slide {
		background-color: #3A115A;
	}
}

@media (max-width: 700px) {
	.banner__buttons {
		display: none;
	}
	.banner__dots {
		left: 50%;
		right: auto;
		top: auto;
		bottom: 6px;
		transform: translateX(-50%);
	}
	.banner__slide-title {
		font-size: 32px !important;
		text-align: center;
		margin-bottom: 15px;
	}
	.banner__slide {
		padding: 55px 20px 30px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		background-image: url(../img/slide.png);
		background-position-x: center;
		background-position-y: bottom;
	}
	.banner__slide-text {
		font-size: 16px;
		text-align: center;
		font-weight: 500;
	}
	.banner__slide-button {
		margin: auto;
		margin-bottom: 0;
	}
}